* {
  font-family: arial, Helvetica Neue, sans-serif;
  box-sizing: border-box;
  margin: 0;
}

.header {
  display: flex;
  box-shadow: 0 15px 40px -20px rgba(40, 44, 63, 0.15);
  background-image: linear-gradient(45Deg,#F97B22,#f09656);
  
  /* flex-direction: row; */
  justify-content: space-between;
  /* border: 2px solid black; */
  margin: 0px;

  /* position: fixed;
  top: 0; */
  height: 80px;
  width: 100%;
  z-index: 1;
  margin-bottom: 20px;
}

.right {
  display: flex;
  /* border:2px solid; */
  align-items: center;
  /* justify-items:center; */
}

.right button {
  height: 30px;
  width: 80px;
}

.nav-list {
  display: flex;
  flex-direction: row;
  list-style-type: none;
}

.logo {
  position: relative;
  height: 100%;
  width: auto;
  padding: 5px 10px;
}

.nav-list li {
  padding: 10px;
}

.nav-list li a {
  color: #ffffff;
  font-weight: 500;
  display: inline-block;
}

.nav-list li a:hover {
  color: rgba(255, 166, 0, 0.859);
}

a {
  text-decoration: none;
}

/* .Body-comp {
  margin-top: 20px;
} */

.search-box {
  display: flex;
  align-items: center;
  justify-content: center;

  /* width:100px; */
}

.search-placeholder {
  width: 300px;
  height: 2rem;
  box-shadow: 0 15px 40px -20px rgba(40, 44, 63, 0.15);
  background-color: #00000008;
  /* border:none; */
}

button {
  color: #60b246;
  width: 5rem;
  height: 2rem;
  cursor: pointer;
  border: white;
  border-radius: 0.2rem;
  box-shadow: 0 15px 40px -20px rgba(40, 44, 63, 0.15);
  margin-left: 6px;
  margin-right: 6px;
}
button:hover {
  color: rgba(255, 166, 0, 0.859);
}

.body-2 h2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 5rem;
  margin-bottom: 10px;
  margin-top: 10px;
}
.body {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.card {
  width: 250px;
  height: 280px;

  padding: 10px;
  margin: 10px;
  background-color: #FEE8B0;
 box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.card:hover {
  background-color: #00000041;
}

.card h3 {
  color: rgba(0, 0, 0, 0.889);
}

.card h6 {
  color: rgba(0, 0, 0, 0.544);
}

.card-img {
  width: auto;
  height: 160px;
}

.card-img img {
  position: relative;
  width: 100%;
  height: 100%;

  /* background-size: cover; */
  /* width: 100%; */
  /* display: none; */
}

.btnclass {
  margin-top: 5px;
  /* border: 2px solid greenyellow; */
}

.shimer {
  display: flex;
  flex-wrap: wrap;
}
.shimer-body {
  /* border: 2px solid black; */
  background-color: rgba(0, 0, 0, 0.199);
  width: 200px;
  height: 250px;
  /* border: 2px solid; */
  padding: 10px;
  margin: 10px;
}

.error-page {
  border: 2px solid;
  display: flex;
  flex-direction: column;
  justify-self: center;
}
.error-pic {
  width: 200px;
  height: auto;
}

.null-restourants {
  display: flex;
  /* border: 2px solid;. */
  margin-top: 10%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: rgba(0, 0, 0, 0.81);
}

.null-restourants img {
  width: 250px;
  height: auto;
}

.null-restourants h3 {
  color: rgba(0, 0, 0, 0.341);
}

.footer {
  position: relative;
  bottom: 0;
  height: 400px;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 80px;
  box-shadow: 0 15px 40px -20px rgba(40, 44, 63, 0.15);
  background-image: linear-gradient(45Deg,#F97B22,#f09656);
}

.display-size {
  height: 76vh;
}

.offline {
  height: 76vh;
  background-color: #60b246;
}
